.category-block {
	@include css-lock(55, 144, 992, 1599, "padding-top");
	@include r(991) {
		padding-top: 54px; }
	@include r(767) {
		padding-top: 50px; } }
.category__h3 {
	@include css-lock(46, 68, 992, 1599, "margin-bottom"); }
.category__h4 {
	transition: color .3s;
	@include css-lock(0, 23, 992, 1599, "margin-top");
	@include css-lock(20, 30, 992, 1599);
	@include r(991) {
		margin-top: 23px;
		font-size: 30px; }
	@include r(560) {
		font-size: 20px;
		margin-top: 0px; } }
.category__list {
	text-align: center; }
.category__item {
	border: 1px solid #f1f1f1;
	margin-bottom: 58px;
	position: relative;
	@include css-lock(300, 484, 992, 1599, "height");
	@include css-lock(10, 55, 992, 1599, "padding-top");
	transition: all .3s;
	&:hover {
		border-color: $pink;
		.category__h4 {
			color: $pink; } }
	@include r(991) {
		height: 484px;
		padding-top: 55px;
		&:last-of-type {
			margin-bottom: 0!important; } }
	@include r(560) {
		padding-top: 10px;
		height: 245px; }
	a {
		display: block;
		height: 100%; } }
.category__img {
	width: 448px;
	margin: 0 auto;
	height: 335px;
	@include cover;
	background-size: contain;
	@include css-lock(270, 448, 992, 1599, "width");
	@include css-lock(240, 335, 992, 1599, "height");
	@include r(991) {
		width: 448px;
		height: 335px; }
	@include r(560) {
		width: 240px;
		height: 200px; } }
