$icons: (0:0);
$icons: map-merge($icons,('bag': (X: 0px, Y:-66px, W: 17px, H: 19px, TW: 105px, TH: 85px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('car': (X: -30px, Y:0px, W: 25px, H: 16px, TW: 105px, TH: 85px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('certificate': (X: -65px, Y:0px, W: 20px, H: 18px, TW: 105px, TH: 85px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('facebook': (X: -95px, Y:0px, W: 10px, H: 19px, TW: 105px, TH: 85px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('facebook@2x': (X: 0px, Y:0px, W: 20px, H: 38px, TW: 105px, TH: 85px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('magnifier': (X: -53px, Y:-66px, W: 14px, H: 14px, TW: 105px, TH: 85px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('next': (X: 0px, Y:-48px, W: 34px, H: 8px, TW: 105px, TH: 85px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('prev': (X: -44px, Y:-48px, W: 34px, H: 8px, TW: 105px, TH: 85px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('reload': (X: -27px, Y:-66px, W: 16px, H: 16px, TW: 105px, TH: 85px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }

