.catalog {
	padding-top: 48px; }
.ctopline {
	background: $catalog-topline-color; }
.ctopline__wrapper {
	@include clr;
	padding: 14px 8px 14px 18px;
	font-size: 0;
	@include vertical; }
.ctopline__left, .ctopline__right {
	display: inline-block;
	width: 50%;
	vertical-align: middle; }
.ctopline__left {
	text-align: left; }
.ctopline__right {
	text-align: right; }
.sorting {
	font-size: 18px;
	.select_wrapper {
		margin-right: 24px;
		min-width: 173px;
		&:last-of-type {
			margin-right: 0;
			min-width: auto; } }
	.select-view {
		padding: 0 30px 0 0; } }
.catalog__quantity {
	display: inline-block;
	font-family: $PS;
	font-size: 26px; }
.catalog__h1 {
	margin-right: 34px; }
.catalog__quantity {
	color: $gray; }
.catalog__container {
	@include clr; }
.sorting__title {
	color: #cacaca;
	font-family: $PR;
	margin-right: 26px; }
.cfilter {
	width: 280px;
	float: left; }
.clist {
	float: right;
	max-width: 1257px; }
.cfilter__options {
	padding-bottom: 37px;
	border-top: 1px solid #dfdfdf;
	&:first-of-type {
		border-top: none; } }
.cfilter__title {
	color: #9f9f9f;
	font-family: $PS;
	font-size: 20px;
	line-height: 1em; }
.check-block {}
.check-block__input {
	display: none;
	&:checked + label >.check-block__icon {
		&:before {
			background: $pink; } } }
.check-block__text {
	@include nosel;
	display: inline-block;
	color: $black;
	font-family: $PR;
	font-size: 18px;
	line-height: 1.78em;
	cursor: pointer; }
.check-block__icon {
	margin-right: 12px;
	display: inline-block;
	cursor: pointer;
	width: 25px;
	height: 25px;
	background-color: #f3f3f3;
	border: 1px solid #e6e6e6;
	position: relative;
	top: 5px;
	&:before {
		content: '';
		transition: all .2s;
		@include vcenter;
		width: 13px;
		height: 13px;
		background: transparent; }
	&:hover {
		&:before {
			background-color: #e6e6e6; } } }
